@import "../generalSettings/animations";
@import "../generalSettings/font";
@import "../generalSettings/colors";

.headerContainer {
    margin-bottom : 60px;
    &__logo{
        width:200px;
        height:150px;
        &__file{
            object-fit: contain;
            width:100%;
            height:100%;
        }
    }
}
.menu{
    display: flex;
    justify-content: space-around;
    padding: 0px 30px;
    &__nav-icon{
        color:$main-font-color;
        // border:1px solid yellowgreen;
        padding:30px;
        display: flex;
        justify-content: center;
        font-size: 2em;
        &__reactIcon{
            cursor: pointer;
            transform:scale(1);
            transition: 500ms;
            // border:1px solid blue;
            display: flex;
            align-items: center;
            &:hover{
                transform: scale(1.3);
            }
        }
    }
    &__logo{
        width:250px;
        height:150px;
        @media screen and (max-width:500px) {
            width:150px;
            height:80px;
        }
        &__file{
            width:100%;
            height:100%;
            object-fit: contain;
        }
    }
}
nav{
    width:75%;
    margin: auto;
    padding: 15px;
    // border: 1px solid black;
    background-color: black;
    border-top : $nav-border;
    border-bottom : $nav-border;
    transform: translateX(-100vw);
    animation: navApparition 600ms linear forwards;
    @media screen and (max-width:600px) {
        width: 85%;
    }
}
.nav {
    display: flex;
    justify-content: space-around;
    // border:1px solid purple;
    &__title{
        font-family: $nav-font;
        font-size: 1.8em;
        color:$main-font-color;
        text-shadow: $text-shadow;
        animation: 1500ms ease-in-out apparition forwards;
        // border: 1px solid red;
        cursor: pointer;
        opacity:0;
        transition: 500ms;
        &:hover{
            transform:scale(1.2);
        }
        &:active{
            transform:scale(0.9);
        }
        @media screen and (max-width:500px) {
            font-size: 1.4em;
        }
    }
    @media screen and (max-width:400px) {
        flex-direction: column;
        text-align: center;
    }
}



